@font-face {
  font-family: "Averta Demo PE Cutted Demo";
  font-style: normal;
  font-weight: 400;
  src: url(/themes/myproject/fonts/averto/Averto.ttf);
}
.astudio__site-logo {
  position: absolute;
  left: 7.1rem;
}
@media (min-width: 0) and (max-width: 768px) {
  .astudio__site-logo {
    display: none;
  }
}

.astudio__menu-link {
  display: flex;
  justify-content: flex-end;
  list-style: none;
  margin-top: 26px;
}
.astudio__menu-link-li {
  margin-right: 20px;
}
.astudio__menu-link-a {
  font-family: "Averta Demo PE Cutted Demo";
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  color: #000000;
  letter-spacing: 0.1em;
  text-decoration: none;
}
.astudio__menu-link-a:hover {
  text-decoration: none;
}
@media (min-width: 0) and (max-width: 768px) {
  .astudio__menu-link {
    flex-direction: column;
  }
}